import React from "react";
import classNames from "classnames";
import style from "../assets/scss/index.module.scss";
import {Link} from "gatsby";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faTag} from "@fortawesome/free-solid-svg-icons";


const Tags: React.FunctionComponent<any> = React.memo<any>((props: any) => {
    return <ul className={classNames(style.uListInline, style.mb0, style.textUppercase, props.className)}>
        {
            props.tags.sort((x: string) => x).map((item: string) => {
                return <li className={classNames(style.listInlineItem, style.gMb10)} key={item}>
                    <Link className={classNames(style.uTagsV1,
                        style.gColorGrayDarkV4,
                        style.gColorWhiteHover,
                        style.gBgGrayLightV5,
                        style.gBgPurpleHover,
                        style.gFontSize12,
                        style.gRounded50,
                        style.gPy4,
                        style.gPx15)}
                          to={`/tags/${item}`}>
                        <FontAwesomeIcon width={8} className={style.mr2} icon={faTag}/>
                        {item}
                    </Link>
                </li>
            })
        }
    </ul>
});

export default Tags;