import React from "react";
import classNames from "classnames";
import * as style from "../assets/scss/index.module.scss";
import SEO from "./seo";
import PropTypes from "prop-types";
import {graphql, Link, useStaticQuery} from "gatsby";
import {library} from "@fortawesome/fontawesome-svg-core";
import {fas} from "@fortawesome/free-solid-svg-icons";
import {fab} from "@fortawesome/free-brands-svg-icons";
import {Header} from "./header";
import Img from "gatsby-image";

const Layout: React.FunctionComponent<any> = (props: any) => {
    library.add(fas, fab);
    const data = useStaticQuery(graphql`
        query {
            imageSharp(fluid: {originalName: {eq: "background.jpg"}}) {
                fluid(maxWidth: 900, maxHeight: 330) {
                    ...GatsbyImageSharpFluid
                }
            }
        }
    `)

    return <>
        <SEO title={props.title} description={props.description}/>
        <section className={classNames(style.gLayoutBoxed, style.gBgiV8)}>
            <main className={classNames(style.container)}>
                <div className={classNames(style.row, style.justifyContentBetween)}>
                    <section className={classNames(style.colLg3, style.orderLg1, style.gMb80Lg, style.gPt100Lg)}>
                        <Header/>
                    </section>
                    <section className={classNames(style.colLg9, style.orderLg9, style.gMb80, style.gPx0)}>
                        <section className={classNames(style.gMb80)}>
                            <Img fadeIn={true}
                                 fluid={data.imageSharp.fluid}
                                 alt={"background image"}/>
                        </section>
                        <section className={classNames(style.col12)}>
                            {props.children}
                        </section>
                    </section>
                </div>
            </main>
        </section>
    </>
}

Layout.propTypes = {
    title: PropTypes.string,
    description: PropTypes.string,
}

export default Layout;