import classNames from "classnames";
import * as style from "../assets/scss/index.module.scss";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import React from "react";
import {graphql, Link, useStaticQuery} from "gatsby";
import {IconName} from "@fortawesome/fontawesome-common-types";

type props = {
    icon: IconName;
    name: string;
    link: string;
}


const Links: React.FunctionComponent = () => {

    const data = useStaticQuery(graphql`
        query {
            site {
                siteMetadata {
                    links {
                        icon {
                            prefix
                            iconName
                        }
                        link
                        name,
                        external
                    }
                }
            }
        }
    `)

    return <>
        {
            data.site.siteMetadata.links.map((item: any, index: number) => {
                return <li key={index} className={classNames(style.navItem, style.gMy5)}>
                    {
                        item.external ?
                            <a className={classNames(style.dBlock, style.uLinkV5, style.gColorGrayDarkV4, style.rounded, style.gPy8, style.gColorPurpleHover)}
                                href={item.link} target={"_blank"}>
                                <FontAwesomeIcon width={14.61} height={12.98} className={style.mr2} icon={item.icon}/>
                                {item.name}
                            </a> :
                            <Link
                                className={classNames(style.dBlock, style.uLinkV5, style.gColorGrayDarkV4, style.rounded, style.gPy8, style.gColorPurpleHover)}
                                to={item.link}>
                                <FontAwesomeIcon width={14.61} height={12.98} className={style.mr2} icon={item.icon}/>
                                {item.name}
                            </Link>
                }
                </li>
            })
        }

    </>
}

export default Links;